.tabBar {
  @apply flex 
  md:hidden 
  flex-row 
  items-center 
  justify-around 
  px-8 
  h-24 
  bg-white 
  visible 
  md:invisible 
  fixed 
  bottom-0 
  w-full 
  rounded-t-3xl 
  text-2xl
  z-50;
}

.tabItem {
  @apply text-gray-400 
  hover:text-gray-700 
  cursor-pointer 
  w-20
  h-full 
  flex 
  items-center 
  justify-center;
}

.tabItemActive {
  @apply bg-gradient-to-t 
  from-white 
  to-gray-100 
  border-t-2 
  pt-3
  border-gray-700 
  text-gray-700;
}

.icon {
  @apply -mb-1;
}
